import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router);
export default new Router({
    mode: 'history',
    scrollBehavior(to, from, savedPosition) {
        return {x: 0, y: 0}
    },
    routes:[
        // {
        //     path: '/:id',
        //     name: 'index',
        //     component: ()=>import('./view/id')
        // },
        {
            path:'/',
            name:'index',
            component:()=>import('./view/index')
        },
        {
            path:'/provide-detail',
            name:'provide-detail',
            component:()=>import('./view/index/detail')
        },
        {
            path:'/member',
            name:'member',
            component:()=>import('./view/member')
        },
        {
            path:'/member/admin-list',
            name:'admin-list',
            component:()=>import('./view/admin-list')
        },
        {
            path:'/company',
            name:'company',
            component:()=>import('./view/company')
        }
    ]
})