<template>
    <el-aside width="230px" class="noSet">
        <div class="company-info ac">
            福分通
        </div>
        <el-input
                size="small"
                placeholder="输入关键字进行搜索"
                prefix-icon="el-icon-search"
                v-model="filterText">
        </el-input>
        <el-menu
                :router="true"
                background-color="#111b25"
                text-color="#fff"
                :default-active="ActiveRoute"
        >
            <el-menu-item index="/member/admin-list">
                <i class="el-icon-setting"></i>
                <span slot="title">管理员列表</span>
            </el-menu-item>
            <el-menu-item index="/member">
                <i class="el-icon-setting"></i>
                <span slot="title">员工列表</span>
                <i :class="['el-submenu__icon-arrow',ActiveRoute === '/member'?'el-icon-arrow-down':'el-icon-arrow-up']"></i>
            </el-menu-item>
        </el-menu>
        <div class="member-list" v-if="ActiveRoute === '/member'">
            <el-tree
                    class="filter-tree"
                    :data="options"
                    :props="defaultProps"
                    :filter-node-method="filterNode"
                    @node-click="nodeClick"
                    ref="tree2">
            </el-tree>
        </div>
    </el-aside>
</template>

<script>
    import  { mapState } from 'vuex'
    export default {
        name: 'aside-nav',
        data() {
            return {
                filterText:'',
                data2: [{
                    id: 1,
                    label: '一级 1',
                    children: [{
                        id: 4,
                        label: '二级 1-1',
                        children: [{
                            id: 9,
                            label: '三级 1-1-1'
                        }, {
                            id: 10,
                            label: '三级 1-1-2'
                        }]
                    }]
                }, {
                    id: 2,
                    label: '一级 2',
                    children: [{
                        id: 5,
                        label: '二级 2-1'
                    }, {
                        id: 6,
                        label: '二级 2-2'
                    }]
                }, {
                    id: 3,
                    label: '一级 3',
                    children: [{
                        id: 7,
                        label: '二级 3-1'
                    }, {
                        id: 8,
                        label: '二级 3-2'
                    }]
                }],
                defaultProps: {
                    children: 'children',
                    label: 'label'
                }
            }
        },
        methods: {
            filterNode(value, data) {
                if (!value) return true;
                return data.label.indexOf(value) !== -1;
            },
            nodeClick(a,b,c){
                this.$store.dispatch('memberList/setDepartment',a.label)
            }
        },
        watch: {
            filterText(val) {
                this.$refs.tree2.filter(val);
            }
        },
        computed:{
            ActiveRoute(){
                return this.$route.path
            },
            ...mapState('user-dialog',['options'])
        }
    }
</script>

<style lang="stylus">
    .el-aside
        min-height 810px
        padding 5px
        border-radius 5px
        background-color #111b25
        .el-menu
            border-right none
        .company-info
            height 100px
            color white
        .el-input
            display block
            margin 0 auto 10px
            width 200px
        .member-list
            margin 0 auto
            border-radius 4px
            padding 5px
            width 200px
            height 400px
            background-color #111b25
            overflow-x scroll
            .el-tree
                background-color #111b25
                color white
                .el-checkbox__inner
                    background-color #111b25
            .el-tree-node__content:hover,.el-tree-node:focus>.el-tree-node__content
                background-color #1c394b !important

</style>
