export const mutations = {
    [SHOWWORKER](state){
        state.worker.edit = !state.worker.edit
    },
    [SHOWORGANIZE](state){
        state.organize.edit = !state.organize.edit
    }
};
export const actions = {
    initWorker({commit,state},req){
        if (req.type === 'add'){
            state.worker.edit = true;
            state.worker.type = '新增员工';
            state.worker.form =
                {
                    name:'',
                    ID:'',
                    phone:'',
                    mail:'',
                    sex:'',
                    entry:'',
                    birth:'',
                    position:'',
                    oragnize:[]
                }
        }else{
            state.worker.edit = true;
            state.worker.type = '编辑员工信息';
            state.worker.form =
                {
                    name:'老王',
                    ID:'9527',
                    phone:'13888888888',
                    mail:'yhjkag@gmail.com',
                    sex:'man',
                    entry:'',
                    birth:'',
                    position:'总监',
                    oragnize:[]
                }
        }
    },
    closeWorker({commit}){
        commit(SHOWWORKER)
    },
    initOrganize({commit,state},req){
        if (req.type === 'add'){
            state.organize.edit = true;
            state.organize.type = 'add';
            state.organize.name = ''
        }else{
            state.organize.edit = true;
            state.organize.type = 'edit';
            state.organize.name = req.name
        }
    },
    closeOrganize({commit}){
        commit(SHOWORGANIZE)
    },
    initOptions({state}){

    }
}

