export const ASIDE ='ASIDE';
export const RSD = 'RSD';
export const state =()=>{
    return {
        aside:'default',
        r_side:'default'
    }
};
export const mutations = {
    [ASIDE](state,res){
        state.aside = res
    },
    [RSD](state,rsd){
        state.r_side = rsd
    }
};
export const actions= {
    SET_ASIDE({commit},res){
        commit(ASIDE,res)
    },
    SET_RSD({commit},rsd){
        commit(RSD,rsd)
    }
}